var toWav = require("audiobuffer-to-wav");

export function DataURIToBlob(dataURI) {
  // convert base64/URLEncoded data component to raw binary data held in a string
  var byteString;
  if (dataURI.split(",")[0].indexOf("base64") >= 0) {
    byteString = atob(dataURI.split(",")[1]);
  } else {
    byteString = unescape(dataURI.split(",")[1]);
  }
  // separate out the mime component
  var mimeString = dataURI.split(",")[0].split(":")[1].split(";")[0];

  // write the bytes of the string to a typed array
  var ia = new Uint8Array(byteString.length);
  for (var i = 0; i < byteString.length; i++) {
    ia[i] = byteString.charCodeAt(i);
  }
  return new Blob([ia], { type: mimeString });
}

export const handleRes = (string) => {
  if (!string) {
    return [];
  }
  if (typeof string === "string") {
    string = string.slice(1, string.length - 1);
    return string.split(", ").map((a) => a.slice(1, a.length - 1));
  }
  if (typeof string === "object") {
    return string;
  }
};

const numberOfChannels = 1;
const sampleRate = 44100;
const duration = 8;
export const addAudioVideo = (blob, form) => {
  // const isFile = typeof blob.name == "string";
  const isWebm = blob.type.indexOf("webm") > -1;
  // console.info(blob, isFile);
  if (isWebm) {
    var offlineAudioContext = new OfflineAudioContext(
      numberOfChannels,
      sampleRate * duration,
      sampleRate
    );
    var soundSource = offlineAudioContext.createBufferSource();
    var audioContext = new (window.AudioContext || window.webkitAudioContext)();
    blob.arrayBuffer().then((audioBuffer) => {
      audioContext
        .decodeAudioData(audioBuffer)
        .then(function (decodedAudioData) {
          const myBuffer = decodedAudioData;
          soundSource.buffer = myBuffer;
          soundSource.connect(offlineAudioContext.destination);
          soundSource.start();
          offlineAudioContext.startRendering().then(function (renderedBuffer) {
            var wav = toWav(renderedBuffer);
            // download(wav, 'audio.wav',"audio/wav")
            var _blob = new window.Blob([new DataView(wav)], {
              type: "audio/wav",
            });
            _blob.name = "audio.wav";
            form.append("audio", _blob);
          });
        });
    });
  } else {
    form.append("video", blob);
  }
  return form;
};

export const addPhotoAndQuery = (shouldExecute, form, query, handleRes) => {
  const video = document.querySelector("video");
  video.onplay = function () {
    setTimeout(() => {
      if (!shouldExecute) {
        return;
      }
      shouldExecute = false;
      const canvas = document.createElement("canvas");
      const photo = document.createElement("img");
      document.body.appendChild(photo);
      var context = canvas.getContext("2d");
      canvas.width = 300;
      canvas.height = 300;
      context.drawImage(video, 0, 0, 300, 300);
      var Photo = canvas.toDataURL("image/png");
      const img = DataURIToBlob(Photo);
      form.append("img", img);
      query(form).then((data) => {
        handleRes(data);
      });
    }, 1000);
    return shouldExecute;
  };
};

