import React, { createContext, useContext } from "react";
import { rootContext } from "./RootContext";
import axios from "axios";
import { server } from "../Consts";
const axiosInstance = axios.create();

export const queryContext = createContext({});
const Provider = queryContext.Provider;

const QueryProvider = ({ children }) => {
  const { setSnackbar } = useContext(rootContext);
  const search = async (params) => {
    const res = await axiosInstance.post(server.search, params).catch((err) => {
      setSnackbar({ open: true, message: "receive bad request" });
    });
    return res && res.data;
  };
  const insert = async (params) => {
    const res = await axiosInstance.post(server.insert, params);
    return res && res.data;
  };
  return <Provider value={{ search, insert }}>{children}</Provider>;
};

export default QueryProvider;
