import React, { useState, useEffect, useRef, useContext } from "react";
import { makeStyles } from "@material-ui/styles";
import { queryContext } from "./contexts/QueryContext";
import { rootContext } from "./contexts/RootContext";
import VideoRecorder from "react-video-recorder";
import Spinner from "./components/Spinner";
import "./Identity.scss";
import { Logo } from "./Icons";
import Button from "@material-ui/core/Button";
import TouchAppIcon from "@material-ui/icons/TouchApp";
import {
  countdownTime,
  Limit_Record,
  route,
  Text_Read,
  Text,
  color,
} from "./Consts";
import {
  handleRes,
  addAudioVideo,
  addPhotoAndQuery,
  getButtonText,
} from "./Helper";
const button_text = getButtonText(`Click To Identify`);
const text_confirm = `Back to Identity Page`;

const Identity = () => {
  const classes = makeStyles({
    buttonRoot: {
      margin: "32px 0 0 0",
      color: color.text,
    },
    contained: {
      background: color.main,
    },
  })({});
  const { search } = useContext(queryContext);
  const { setSnackbar } = useContext(rootContext);
  const [loading, setLoading] = useState(false);
  const [cameraOn, setCameraOn] = useState(false);
  const [recording, setRecording] = useState(false);
  const [valid_res, setValidRes] = useState();
  const Link = useRef(null);
  const Form = useRef(new FormData());
  const isFirstRun = useRef(true);
  const showLogo = !loading && !cameraOn && !recording;

  useEffect(() => {
    setTimeout(() => {
      let button = document.querySelector(".button__Button-hkteey-0");
      let container =
        document.querySelector(".hCBOlN") || document.querySelector(".hSoFMV");
      if (button) {
        button.innerHTML = button_text;
        const div = document.createElement("div");
        const link = document.createElement("a");
        link.href = route.register;
        link.className = "a_link";
        link.innerHTML = ` New User? `;
        link.style.margin = "0 0 0 5px";
        div.appendChild(link);
        Link.current = div;
        container.appendChild(div);
      }
    }, 0);
  }, []);
  const onTurnOnCamera = () => {
    setCameraOn(true);
    Link.current && Link.current.classList.add("hidden");
    const camera = document.querySelector(".heQTiL");
    const video = document.querySelector(".bVxZhR");
    camera && camera.classList.add("full-height");
    video && video.classList.add("full-height");
  };
  const resolveRes = (data) => {
    const arr = handleRes(data);
    if (Array.isArray(arr)) {
      if (arr[0] === "true") {
        setValidRes({ src: arr[1], name: arr[2] });
      } else {
        setSnackbar({
          open: true,
          message: Text.unAuthorized,
          onClose: () => window.location.reload(),
        });
      }
    } else {
      setSnackbar({
        open: true,
        message: (arr && arr.message) || Text.fail_to_identify,
      });
      setTimeout(() => {
        window.location.reload();
      }, 3000);
    }
  };
  // Attention: mobile phone's won't don this step, they have to use the video recorded after complete;
  const onStartRecording = () => {
    setCameraOn(false);
    setRecording(true);
  };
  const onRecordingComplete = (videoBlob) => {
    Form.current = addAudioVideo(videoBlob, Form.current);
    isFirstRun.current = addPhotoAndQuery(
      isFirstRun.current,
      Form.current,
      search,
      resolveRes
    );
    setRecording(false);
    setLoading(true);
    const link = document.querySelector(".a_link");
    link && link.classList.add("hidden");
    const video = document.querySelector("video");
    video && video.classList.add("hidden");
    const button = document.querySelector("button");
    button && button.classList.add("hidden");
  };
  return (
    <div className="App">
      {showLogo && (
        <>
          <div className="margin-top"></div>
          <div
            className="disconnect-view"
            dangerouslySetInnerHTML={{ __html: Logo }}
          ></div>
          <h3>ID Systems powered by Milvus</h3>
          <p className="label">{Text.note_to_record}</p>
          <p className="label">
            {Text.for_example} &nbsp;&nbsp;<span>{Text_Read}</span>
          </p>
        </>
      )}
      <VideoRecorder
        onTurnOnCamera={onTurnOnCamera}
        onStartRecording={onStartRecording}
        onRecordingComplete={onRecordingComplete}
        showReplayControls={false}
        countdownTime={countdownTime}
        timeLimit={Limit_Record}
        isFlipped={false}
        replayVideoAutoplayAndLoopOff={true}
        renderDisconnectedView={() => <></>}
        renderLoadingView={() => <Spinner />}
      />
      {loading && (
        <div className="mask">
          {valid_res ? (
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                flexDirection: "column",
              }}
            >
              <img src={valid_res.src} alter={valid_res.name} alt="" />
              <h3 style={{ color: "#ffffff" }}>
                Welcome &nbsp;&nbsp;
                <span style={{ color: "#00ff00", fontSize: "2rem" }}>
                  {valid_res.name}
                </span>
                &nbsp;~
              </h3>
              <Button
                classes={{
                  root: classes.buttonRoot,
                  contained: classes.contained,
                }}
                variant="contained"
                size="large"
                onClick={() => {
                  window.location.pathname = "/";
                }}
              >
                <TouchAppIcon />
                {text_confirm}
              </Button>
            </div>
          ) : (
            <Spinner />
          )}
        </div>
      )}
    </div>
  );
};

export default Identity;
