import React, { useState, useEffect, useRef, useContext } from "react";
import { queryContext } from "./contexts/QueryContext";
import { rootContext } from "./contexts/RootContext";
import { makeStyles } from "@material-ui/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import TouchAppIcon from "@material-ui/icons/TouchApp";
import VideoRecorder from "react-video-recorder";
import Spinner from "./components/Spinner";
import "./Identity.scss";
import { Check, Logo } from "./Icons";
import { countdownTime, Limit_Record, color, Text_Read } from "./Consts";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import { addAudioVideo, addPhotoAndQuery, getButtonText } from "./Helper";
import { route, Text } from "./Consts";

const placeholder = `Please Enter your name`;
const button_text = getButtonText(`Click to Record`);
const text_confirm = `Back to Identity Page`;
const text_link = `Registed? `;
const Register = () => {
  const matches = useMediaQuery("(min-width:1000px)");
  const classes = makeStyles({
    root: {
      width: matches ? "30%" : "70%",
      marginBottom: "10px",
      display: "block",
      "& input:valid + fieldset": {
        borderColor: color.hint,
        borderWidth: 2,
      },
      "& input:valid:focus + fieldset": {
        borderLeftWidth: 6,
        padding: "4px !important",
        borderColor: color.main,
      },
      "& .MuiOutlinedInput-root": {
        "&:hover fieldset": {
          borderColor: color.main,
        },
      },
    },
    input: {
      color: color.text,
      width: `100%`,
    },
    buttonRoot: {
      margin: "32px 0 0 0",
      color: color.text,
    },
    contained: {
      background: color.main,
    },
  })({});
  const { insert } = useContext(queryContext);
  const { setSnackbar } = useContext(rootContext);
  const [name, setName] = useState("");
  const [cameraOn, setCameraOn] = useState(false);
  const [recording, setRecording] = useState(false);
  const [loading, setLoading] = useState(false);
  const [valid_res, setValidRes] = useState();
  const Link = useRef(null);
  const buttonRef = useRef(null);
  const isFirstRun = useRef(true);
  const Form = useRef(new FormData());
  const showLogo = !loading && !cameraOn && !recording;
  useEffect(() => {
    setTimeout(() => {
      let button = document.querySelector(".button__Button-hkteey-0");
      let container =
        document.querySelector(".hCBOlN") || document.querySelector(".hSoFMV");
      if (button) {
        buttonRef.current = button;
        button.disabled = true;
        button.innerHTML = button_text;
        button.classList.add("not-allowed");
        const div = document.createElement("div");
        const link = document.createElement("a");
        link.href = route.identity;
        link.className = "a_link";
        link.innerHTML = text_link;
        link.style.margin = "0 0 0 5px";
        div.appendChild(link);
        Link.current = div;
        container.appendChild(div);
      }
    }, 0);
  }, []);
  const resolveRes = (data) => {
    if (data && data.status === "success") {
      setValidRes(true);
    } else {
      setSnackbar({
        open: true,
        message: (data && data.message) || Text.fail_to_insert,
        onClose: () => window.location.reload(),
      });
    }
  };
  const onNameChange = (e) => {
    const value = e.target.value;
    if (buttonRef.current) {
      buttonRef.current.disabled = !value;
      value
        ? buttonRef.current.classList.remove("not-allowed")
        : buttonRef.current.classList.add("not-allowed");
    }
    setName(value);
  };
  const onTurnOnCamera = () => {
    setCameraOn(true);
    Link.current && Link.current.classList.add("hidden");
    const camera = document.querySelector(".heQTiL");
    const video = document.querySelector(".bVxZhR");
    camera && camera.classList.add("full-height");
    video && video.classList.add("full-height");
  };
  const onStartRecording = () => {
    setCameraOn(false);
    setRecording(true);
  };
  const onRecordingComplete = (videoBlob) => {
    setRecording(false);
    setLoading(true);
    Form.current.append("Name", name);
    Form.current = addAudioVideo(videoBlob, Form.current);
    isFirstRun.current = addPhotoAndQuery(
      isFirstRun.current,
      Form.current,
      insert,
      resolveRes
    );
    const video = document.querySelector(".heQTiL");
    video && video.classList.add("hidden");
    const video_build = document.querySelector(".bVxZhR");
    video_build && video_build.classList.add("hidden");
  };
  return (
    <div className="App">
      {showLogo && (
        <>
          <div className="margin-top"></div>
          <div
            className="disconnect-view"
            dangerouslySetInnerHTML={{ __html: Logo }}
          ></div>
          <h3>ID Systems powered by Milvus</h3>
          <TextField
            variant="outlined"
            placeholder={placeholder}
            value={name}
            onChange={onNameChange}
            classes={{
              root: classes.root,
            }}
            InputProps={{
              className: classes.input,
            }}
          />
          <p className="label">{Text.note_to_record}</p>
          <p className="label">
            {Text.for_example} &nbsp;&nbsp;<span>{Text_Read}</span>
          </p>
        </>
      )}
      <VideoRecorder
        onTurnOnCamera={onTurnOnCamera}
        onStartRecording={onStartRecording}
        onRecordingComplete={onRecordingComplete}
        showReplayControls={false}
        countdownTime={countdownTime}
        timeLimit={Limit_Record}
        isFlipped={false}
        replayVideoAutoplayAndLoopOff={true}
        renderDisconnectedView={() => <></>}
        renderLoadingView={() => <Spinner />}
      />
      {loading && (
        <div className="mask">
          {valid_res ? (
            <>
              <div
                className="result"
                dangerouslySetInnerHTML={{ __html: Check }}
              ></div>
              <Button
                classes={{
                  root: classes.buttonRoot,
                  contained: classes.contained,
                }}
                variant="contained"
                size="large"
                onClick={() => {
                  window.location.pathname = "/";
                }}
              >
                <TouchAppIcon />
                {text_confirm}
              </Button>
            </>
          ) : (
            <Spinner />
          )}
        </div>
      )}
    </div>
  );
};

export default Register;
